<template>
  <div>
    <headers v-bind:header-name="header"
             v-bind:category-i-d="this.$route.params.categoryId"></headers>
    <div style="margin: 20px 20px 0 20px;">
      <step step3=true></step>
    </div>
    <video controls="controls" :src="conversation.video2" type='video/mp4'></video>
    <div class="video-btn">
      <img src="../../assets/microphone.png">
    </div>
    <p class="video-text"> </p>
  </div>
</template>

<script>
import axios from 'axios';
import Headers from '../Header_back';
import Step from './Step_selector';

export default {
  components: {
    Headers,
    Step,
  },
  data() {
    return {
      header: '!',
      conversation: {
        id: 1,
        title: '!',
        // eslint-disable-next-line
        image: require('../../assets/people/08.png'),
        // eslint-disable-next-line
        video: require('../../assets/video/sample01(sound x).mp4'),
        sentences: [
          {
            id: 1,
            A: {
              chinese_c: ', ',
              chinese_c_hidden: '',
              chinese_e: 'n hoxio lng, zhu m gu de ho ma',
              korean: ', .   ?',
              // eslint-disable-next-line
              audio: require('../../assets/audio/sound_sample.mp3'),
              hidden: true,
            },
            B: {
              chinese_c: '',
              chinese_c_hidden: '',
              chinese_e: 'ng gude hn hon ne',
              korean: ',  .    ?',
              // eslint-disable-next-line
              audio: require('../../assets/audio/sound_sample.mp3'),
              hidden: true,
            },
          },
          {
            id: 2,
            A: {
              chinese_c: '',
              chinese_c_hidden: '',
              chinese_e: '',
              korean: '  .   .',
              // eslint-disable-next-line
              audio: require('../../assets/audio/sound_sample.mp3'),
              hidden: true,
            },
            B: {
              chinese_c: ', ',
              chinese_c_hidden: '',
              chinese_e: 'ng gude hn hon ne',
              korean: ' ',
              // eslint-disable-next-line
              audio: require('../../assets/audio/sound_sample.mp3'),
              hidden: true,
            },
          },
        ],
      },
    };
  },
  created() {
    axios.get(`conversations/${this.$route.params.categoryId}/${this.$route.params.conversationId}/step3`, {
    }).then((response) => {
      this.conversation = response.data;
    });
  },
};
</script>

<style scoped>
  video {
    width: 100%;
    height: 235px;
  }

  .video-btn {
    width: 150px;
    height: 150px;
    border: 1px solid #c21720;
    border-radius: 100px;
    margin: auto;
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(1,1fr);
    align-items: center;
    justify-items: center;
  }

  .video-text {
    font-size: 20px;
    font-weight: lighter;
  }
</style>



