<template>
    <div>
      <headers v-bind:headerName=header></headers>
      <div class="contents-wrapper">
        <h3 class="contents-title-korean"> &lt; {{ content.title_k }} &gt; </h3>
        <h3 class="contents-title-chinese"> &lt;{{ content.title_c }} &gt;</h3>
        <img class="contents-image" :src="content.image">
        <p class="contents-text" v-if="language">{{ content.content_k }}</p>
        <p class="contents-text" v-else>{{ content.content_c }}</p>
        <div style="text-align: right;">
          <img class="contents-btn" src="../../assets/trans.png" v-if="language"
               @click="language=false">
          <img class="contents-btn" src="../../assets/korean.png" v-else
               @click="language=true">
        </div>
      </div>
    </div>
</template>

<script>
import Headers from '../Header_back';

export default {
  components: {
    Headers,
  },
  data() {
    return {
      header: '',
      language: true,
      content: {
        title_k: ' , 128   ',
        title_c: '128',
        // eslint-disable-next-line
        image: require('../../assets/contents/10.jpg'),
        // eslint-disable-next-line
        audio: require('../../assets/audio/sound_sample.mp3'),
        content_k: '<  , 128    >\n' +
        '\n' +
        '    \n' +
        '\n' +
        '     128      .              .       2011 ,        . \n' +
        '\n' +
        '               8m       (Quasar )   9 . \n' +
        '\n' +
        '      .                .      .\n' +
        '\n' +
        '    IMS J2204+0111   .                   .               .\n' +
        '\n' +
        '          .\n' +
        '\n' +
        '\n' +
        ' : http://news.donga.com/3/all/20151110/74685650/1',
        content_c: '< 128 >\n' +
        '\n' +
        '\n' +
        '\n' +
        '1282011\n' +
        '\n' +
        '98Quasar\n' +
        '\n' +
        '\n' +
        '\n' +
        'IMS J2204+0111\n' +
        '\n' +
        '',
      },
    };
  },
};
</script>

<style scoped>
  .contents-wrapper {
    margin: 50px 10px 10px 10px;
    border-radius: 10px;
    background-color: white;
    padding: 20px;
  }

  .contents-title-korean {
    margin: 10px;
    font-weight: lighter;
    font-size: 13px;
  }

  .contents-title-chinese {
    margin: 10px;
    font-weight: bold;
    color: #c21720;
    font-size: 13px;
  }

  .contents-image {
    width: 100%;
    border-radius: 20px;
    margin: 10px 0;
  }

  .contents-text {
    text-align: left;
    white-space: pre-line;
    word-break: break-all;
    letter-spacing: 0.5px;
    font-size: 13px;
    font-weight: lighter;
  }

  .contents-btn {
    width: 40px;
  }
</style>



